			}

		}

}
.tile {
	height : 100%;
	min-height: 60px;
	position: relative;
	overflow: hidden;
	aspect-ratio: 1;
	
	&__back, .back {
		width: 100%;
		height: 100%;
		position: absolute;
		inset: 0;

		&__inner {
			width: 100%;
			height: 100%;
		}

		&__image {
			display: block;
			width: 100%;
			height: 100%;
			object-fit: cover;
		}

	}
	
	&__front, .front {
		width: 100%;
		height: 100%;
		position: absolute;
		transition: all 0.6s ease;
		inset: 100% 0 0 0;
		background-color: $white;
		
		&--green { background-color: $green; }
		&--yellow { background-color: $yellow; }
		&--white { background-color: $white; }
		&--black { background-color: $black; }

		&__inner {
			padding: 24px;
			width: 100%;
			height: 100%;
			display: flex;
			flex-direction: column;
		}

		&__title {
			margin-top: 48px;
		}

		&__link {
			align-self: flex-end;
			margin-top: auto;
			display: block;
		}

		&__title, 
		&__subtitle, 
		&__link {
			opacity: 0;
			position: relative;
			top: 16px;
			transition: all 0.6s ease;
		}
		
	}

	&:hover {
		.tile__front { inset: 0;  }
		.front__title, 
		.front__subtitle, 
		.front__link {
			opacity: 1;
			top: 0px;
			transition-delay: 0.3s;
		}	
		
	}

}
.footer {
	background-color: $black;
	
	&__inner {
		min-height: 390px;
		padding: 30px;
		max-width: $max-width;
		margin: 0 auto;
	}

	&__info-block-wrapper {
		display: grid;
		grid-template: auto / repeat(3, 1fr);
		grid-template-areas:
			"block-1 block-2 block-3";
		grid-gap: 40px;
		padding-bottom: 60px;
		margin-bottom: 30px;
		border-bottom: 1px solid $white;
	}

	&__info-block {

		&--block-1 { grid-area: block-1 }
		&--block-2 { grid-area: block-2 }
		&--block-3 { grid-area: block-3 }

		p, h3 {
			color: $white;
		}

	}

	&__text-item {
		display: block;
		margin-bottom: 7.5px;
	}

	&__newsletter-subscription {
		margin-bottom: 30px;
	}

	&__image {
		margin-bottom: 30px;
		max-width: 100%
	}

	&__link {
		display: block;
	}

	&__social-media-icons {
		margin-bottom: 30px;
	}

	&__language-button {
		padding: 5px 10px;
		margin-right: 15px;
	}

	&__copyright {
		text-align: end;
		color: $white;
	}

	@media (max-width: 750px) {
		&__info-block-wrapper {
			grid-template: auto auto / repeat(2, 1fr);
			grid-template-areas:
				"block-1 block-2"
				"block-3 block-3";
		}
	}
	
	@media (max-width: 500px) {
		&__info-block-wrapper {
			grid-template: auto auto auto / repeat(1, 1fr);
			grid-template-areas:
				"block-1" 
				"block-2"
				"block-3";
		}

	}

}
.tile-set {
	display: grid;
	grid-template-columns: repeat(3, 1fr);
	grid-auto-rows: 1fr;
	

	
	@media (max-width: 850px) {
		grid-template-columns: repeat(2, 1fr);
	}
	
	@media (max-width: 500px) {
		grid-template-columns: repeat(1, 1fr);
	}

}
.slider {
	border: 10px solid right;
	height: 100%;
	position: relative;
	
	.slider__content {
		width: calc(100% - (2 * 80px));
		margin: 0 auto;
    height: 480px;
    max-width: 100%;
    display: flex;
    flex-direction: row;
    position: relative;
    overflow: hidden;
	}
	
	.slider__item {
		width: 100%;
    height: 100%;
    object-fit: cover;
    background-position: 50% 50%;
    padding: 0 1px;
		display: none;
    position: absolute;
    left: 0%;
    opacity: 0;
    transition: all 0.6s ease;
    user-select: none;
		
		&--previous {
			opacity: 1;
			display: block;
			left: -100%;
		}
		
		&--active {
			opacity: 1;
			display: block;
			left: 0%;
		}
		
		&--next {
			opacity: 1;
			display: block;
			left: 100%;
		}
		
	}

		&__arrow {
			position: absolute;
			top: 50%;
			transform: translateY(-50%);
			width: 24px;
    	height: 24px;
    	background-repeat: no-repeat;
    	background-size: contain;
    	transition: all 0.1s ease;
		
		&--left{
			background-image: url("/media/icons/general/arrow-left-slider.svg");
			left: 40px;
		}
		
		&--right{
			background-image: url("/media/icons/general/arrow-right-slider.svg");
			right: 40px;
		}

		&--disabled {
			pointer-events: none;
		}
		
	}
	
	@media (max-width: 500px) {
		
		.slider__content {
			width: 100%;
			height: 320px;
		}

		&__arrow {
			position: absolute;
			top: unset;
			bottom: -60px;
			transform: translateY(-50%);
			width: 24px;
    	height: 24px;
    	background-repeat: no-repeat;
    	background-size: contain;
    	transition: all 0.1s ease;
		
		&--left{
			background-image: url("/media/icons/general/arrow-left-slider.svg");
			left: unset;
			right: 56px;
		}
		
		&--right{
			background-image: url("/media/icons/general/arrow-right-slider.svg");
			right: 24px;
		}

		&--disabled {
			pointer-events: none;
		}
		
	}
	
}

}
.checkbox {
	
	&__option {
		display: inline-flex;
		flex-direction: row;
		justify-content: flex-start;
		align-items: flex-start;
		margin-right: 30px;
		margin-bottom: 15px;
	}

	&__input {
		width: 20px;
    height: 20px;
		min-width: 20px;
		min-height: 20px;
		background-color: $input-background-color;
    appearance: none;
    vertical-align: bottom;
    margin-right: 16px;
		margin-top: 4px;
    background-image: url('/media/icons/form-elements/checkbox-check.svg');
    background-size: 0px;
    background-position: center;
    transition: background-size 0.1s ease;
    background-repeat: no-repeat;
		border: $input-border;

		&:checked {
			background-size: 16px;
			background-color: $black;
		}

	}

	&__label {
		margin-top: 2px;
		line-height: 1.5;
	}

}
.file {

		
	&__wrapper {
		display: grid;
		grid-template: auto / 200px auto;
		grid-template-areas: 
			"button input";
	}

	&__button {
		grid-area: button;
		color: $white;
    display: flex;
    align-items: center;
    justify-content: center;
	 	cursor: pointer;
		padding: $input-padding;
		border-radius: $input-border-radius 0 0 $input-border-radius;
	 	outline: none;
		border: 1px solid $black;
		background-color: $black;
	 	text-align: center;
 	}

	
  &__input {
		width: 0.1px;
		height: 0.1px;
		opacity: 0;
		overflow: hidden;
		position: absolute;
		z-index: -1;
	}

	&__fake-input {
		grid-area: input;
		width: 100%;
		padding: $input-padding;
		border-radius: 0 4px 4px 0 ;
		outline: none;
		border: 1px solid $black;
		border-left: 0;
		color: $black;
		caret-color: $white;
		pointer-events: none;
		background-color: transparent;

		&::placeholder{
			color: $black;
		}
		
		::-ms-input-placeholder {
			color: $green;
		}

	}



	&__input + &__label * {
		pointer-events: none;
	}

	&__error-text {
		grid-area: error;
		color: $red;
		display: none;
	}

	@media (max-width: 600px) {

		&__wrapper {
			display: grid;
			grid-template: auto / 60px auto;
			grid-template-areas: 
				"button input";
		}

		&__button {
			background: url('/media/icons/form-elements/upload-arrow.svg'), $black;
			background-position: center, center;
			background-repeat: no-repeat, repeat;
			background-size: 12px;
			font-size: 0px;
		}

	}

}
.dropdown {
	position: relative;

	&__input {
		background-color: $input-background-color;
		border-radius: $input-border-radius;
		padding: $input-padding;
		outline: $input-outline;
		line-height: 1.5;
		width: $input-width;
		border: $input-border;
		display: block;
		position: relative;

		&::after {
			content: "";
			width: 12px;
			height: 6px;
			position: absolute;
			right: 16px;
			top: 50%;
			background: url('/media/icons/form-elements/dropdown-carret.svg') no-repeat;
		}
	}

	&__checkbox {
		display: none;

		&:checked + .dropdown__options {
			display: flex;
		}
		
	}
	
	&__options {
		display: none;
		border: $input-border;
		border-top: none;
		border-radius: $input-border-radius;
		flex-direction: column;
		overflow: hidden;
		position: absolute;
		width: 100%;
	}

	&__option, .option {
		
		&__input {
			border: $input-border;
			display: none;
		}
		
		&__label {

			display: block;
			padding: $input-padding;
			background-color: $white;
			line-height: 1.5;

			&:hover {
				background-color: $grey;
			}
		
		}

	}
	
}
.radio {
	
	&__option {
		display: inline-flex;
		flex-direction: row;
		justify-content: flex-start;
		align-items: center;
		margin-right: 30px;
		margin-bottom: 15px;
	}

	&__option-wrapper:last-child {
		margin-right: 0px;
	}

	&__input {
		width: 24px;
    height: 24px;
		background-color: $input-background-color;
    border-radius: 50px;
    appearance: none;
    vertical-align: bottom;
    margin-right: 16px;
    background-image: url('/media/icons/form-elements/radio-bullet.svg');
    background-size: 0px;
    background-position: center;
    transition: background-size 0.1s ease;
    background-repeat: no-repeat;
		border: $input-border;

		&:checked {
			background-size: 16px;
		}

	}

	&__label {
		margin-top: 5px;
	}

}
.label {
	display: block;
	margin-bottom: 4px;
}
.range {

	&__label {
		margin-bottom: 12px;
	}

	&__input[type="range"] {
		margin: auto;
		-webkit-appearance: none;
		position: relative;
		height: 4px;
		width: 100%;
		cursor: pointer;
		border-radius: 5px;
		margin-bottom: 32px;
	}

	::-webkit-slider-runnable-track {
		height: 8px;
		border-radius: 0;
	}

	::-webkit-slider-thumb {
		-webkit-appearance: none;
		width: 16px;
		height: 16px;
		border-radius: 50%;
		background: $black;
		position: relative;
		top: -4px;

		&:active {
			cursor: grabbing;
		}

	}

	::-moz-range-track {
		height: 8px;
		border-radius: 0;
	}
	
	::-moz-range-thumb {
		width: 16px;
		height: 16px;
		border-radius: 50%;
		background: $black;
		position: relative;
		top: -4px;
	}

}

.text {

	&__input {		
		background-color: $input-background-color;
		border-radius: $input-border-radius;
		padding: $input-padding;
		outline: $input-outline;
		margin-bottom: $input-margin;
		width: $input-width;
		border: $input-border;
		
		&--error {
			border-color: $red !important;
		}
		
		&--hidden {
			@include hidden;
		}
		
	}

}
.switch {
	width: 48px;
	height: 24px;
	position: relative;
	margin: auto 16px;

	&__input {
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		-moz-opacity: 0;
		opacity: 0;
		z-index: 100;
		position: absolute;
		width: 100%;
		height: 100%;
		cursor: pointer;
	}

	&__background {
		display: block;
		width: 100%;
		height: 100%;
		position: relative;
		background: #cfd8ef;
		border-radius: 15px;
		-webkit-transition: all 0.2s ease;
		transition: all 0.2s ease;
	}

	&__input ~ .switch__background i {
		display: block;
		height: 16px;
		width: 16px;
		position: absolute;
		left: 4px;
		top: 50%;
		transform: translateY(-50%);
		z-index: 2;
		border-radius: inherit;
		background: $white;
		-webkit-transition: all 0.2s ease;
		transition: all 0.2s ease;
	}

	&__input:checked + .switch__background {
		background: $black;
	}
	
	&__input:checked + .switch__background i {
		left: auto;
		left: calc(100% - (14px + 4px));
		-webkit-transition: all 0.2s ease;
		transition: all 0.2s ease;
	}
}

.textarea {

	background-color: $input-background-color;
	border-radius: $input-border-radius;
	padding: $input-padding;
	outline: $input-outline;
	margin: $input-margin;
	width: $input-width;
	border: $input-border;
	resize: none;	

		&--error {
			border-color: $red !important;
		}
		
		&--hidden {
			@include hidden;
		}

}
.link-navigation {
	
	text-decoration:  none;
	color: $black;

	&--green { color: $green; }
	&--white { color: $white; }
	&--black { color: $black; }
	
}
.link-with-arrow {
	display: flex;
	flex-direction: row;
	justify-content: flex-start;
	align-items: center;
	text-decoration:  none;
	
	&__text {
		margin-right: 8px;
		color: $black;
		font-family: 'Roboto-Regular';
	}

	&__icon  {
		font-size: 10px;
		vertical-align: middle;
		color: $black;
		font-weight: 100;
		transition: all 0.1s ease;
		margin-left: 4px;

		&::before {
			vertical-align: baseline;
		}

	}

	&--black {
		.link-with-arrow__text { color: $black; }
		.link-with-arrow__icon { color: $black; }
	}

	&--white { 
		.link-with-arrow__text { color: $white }
		.link-with-arrow__icon { color: $black }
	}

	&:hover {
		
		.link-with-arrow__text {
			color: red;
		}
		
		.link-with-arrow__icon {
			margin-left: 8px;
		}
	
	}

}
.navigation-desktop {

		z-index: 10;
		border-bottom: 1px solid $black;
		z-index: 10;
		position: fixed;
		top: 0;
		right: 0;
		left: 0;
		height: 100px;
		background-color: white;
		
		&__inner {
			display: flex;
			flex-direction: row;
			justify-content: space-between;
			align-items: center;
			padding: 0 30px;
			max-width: 1500px;
			margin: 0 auto;
			height: 100px;
		}

		&__logo-link {
			line-height: 1;
		}
		
		&__logo {
			height: 50px;
		}

		&__link {
			position: relative;
			height: 100%;
			display: flex;
			flex-direction: column;
			justify-content: center;
			padding-right: 40px;
			
			&--has-childs {
				margin-right: 20px;

				&:hover .navigation-desktop__dropdown, 
				+ .navigation-desktop__dropdown:hover {
					display: flex;
				}

				.link__item {
		
						&:after {
							content: url('/media/icons/general/carret.svg');
							width: 8px;
							position: absolute;
							height: 8px;
							display: inline-block;
							margin-left: 4px;
							transform: translateY(25%);
						}
					
					}


			}

		}

		&__links {
			height: 100%;
			display: flex;
			justify-content: center;
			align-items: center;
		}
		
		&__item {
			padding: 8px;
			display: inline-block;
			line-height: 1;
		
		}

		&__dropdown, .dropdown {
			display: none;
			position: absolute;
			top: 100px;
			left: 0;
			flex-direction: column;
			justify-content: flex-start;
			align-items: flex-start;
			background-color: $white;
			border: 1px solid $black;
			border-top: 0;
			transition-delay: 3s;
			
			&__item {
				padding: 8px 16px;
				width: 100%;
				
				&:hover {
					background-color: $grey;
				}
				
			}
		
		}
		
	}
	
	.navigation-desktop__placeholder {
		margin-bottom: 100px;
	}
	
	@media (max-width: 1040px) {
		
		.navigation-desktop, 
		.navigation-desktop__placeholder {
			display: none;
		}

}
	.navigation-mobile {
		display: none;
		z-index: 10;
		position: fixed;
		top: 0;
		right: 0;
		left: 0;
		height: 75px;
		background-color: $white;

		&__inner {
			max-width: $max-width;
			margin: 0 auto;
		}

		&__background-layer {
			background-color: $black;
			opacity: 0;
			position: fixed;
			top: 0;
			right: 0;
			bottom: 0;
			left: 0;
			z-index: -1;
			transition: all 0.3s ease;
			touch-action: none;
			pointer-events: none;
			
			&--active {
				opacity: 0.75;
			}

		}
		
		&__bar {
			display: flex;
			flex-direction: row;
			justify-content: space-between;
			align-items: center;
			padding: 16px;
			height: 75px;
			border-bottom: 1px solid $black;
			background-color: $white;
		}

		&__panel {
			background-color: $grey;
			width: 100%;
			height: 100vh;
			max-width: 500px;
			display: flex;
			flex-direction: column;
			align-items: flex-end;
			position: absolute;
			right: -100%;
			transition: all 0.3s ease;
			overflow: scroll;
			border-left: 1px solid $black;

			&--open {
				right: -0%;
			}

		}

		&__logo-link {
			line-height: 1;
		}
		
		&__logo {
			height: 30px;
		}

		&__links  {
			display: flex;
    	flex-direction: column;
			width: 100%;
			height: 100%;
			justify-content: flex-start;
			align-items: center;
			margin-bottom: 75px;
		}
			
		&__link, .link {
				color: $black;
				border-bottom: 1px solid $black;
				width: 100%;
				
				&__item {
					padding: 16px;
					display: block;
					line-height: 1.5;
				}
				
				&--has-sublinks {
				position: relative;
				
				&::after {
					content: url('/media/icons/general/carret.svg');
					margin-left: 7.5px;
					position: absolute;
					right: 32px;
					top: 16px;
				}

				> * {
					fill: white;
				}
			
			}

		}


		
		&__dropdown, .dropdown {
			max-height: 0px;
			display: none;
			flex-direction: column;
			align-items: flex-end;
			background-color: $white;
			
			&--open {
				max-height: unset !important;
				display: flex !important;
				width: 100%;
			}
			
			&__item {
				color: $black;
				padding: 16px 16px 16px 48px;
				width: 100%;
				border-top: 1px solid $black;

				&:last-child {
					border-bottom: none;
				}
		
		}
		
	}
	
}
	
	.navigation-mobile__placeholder {
		margin-bottom: 75px;
	}
	
	@media (max-width: 1040px) {
		
		.navigation-mobile, 
		.navigation-mobile__placeholder {
			display: block
		}
		
	}
